* {
  box-sizing: border-box;
}

html {
  font-family: assistant;
  // font-size: calc(1vw + 1vh + 1vmin);
  font-size: calc(15px + 0.390625vw);
  width: 100vw;
  // background-color: red;

  & footer {

    #enable-toolbar-trigger-svg {
      width: unset;
    }
  }
}

body {
  // background-color: green;
  margin: 0;
  // overflow-x: hidden;
  min-width: 100%;
  max-height: 100vh;

}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: unset;
}

p {
  margin: unset;
}

a {
  text-decoration: none;
  color: unset;
}

img {
  width: 100%;
}

